.Movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding:3rem;
  box-sizing:border-box;
  width:100%;

  .movie {
    background-color: white;
    width: 45%;
    display: flex;
    margin-bottom:4rem;
    padding:1.5rem;
    box-sizing: border-box;
    align-items: flex-start;
    justify-content: space-between;
    -webkit-box-shadow: 0px 0px 24px 6px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 0px 24px 6px rgba(0,0,0,0.2);
    box-shadow: 0px 0px 24px 6px rgba(0,0,0,0.2);
    img {
      position: relative;
      top:-3rem;
      max-width: 180px;
      width:100%;
      margin-right:1rem;
      -webkit-box-shadow: 0px 30px 60px -12px rgba(0,0,0,0.3);
      -moz-box-shadow: 0px 30px 60px -12px rgba(0,0,0,0.3);
      box-shadow: 0px 30px 60px -12px rgba(0,0,0,0.3);
    }
  }

  .movie-data{
    padding:0 0 0 1rem;
  }

  .movie-title{
    font-size: 2rem;
    font-weight:500;
    margin: 2rem 0 0 0;
  }

  .movie-year{
    color:#999;
    margin:.3rem 0 1rem;
  }

  .genres{
    margin:.3rem 0;
    padding:0;
    list-style: none;
    display: flex;
    color:#999;
    .genre{
      font-size: .85rem;
      & + & {
        margin-right: .3rem;
      }
    }
  }

  .movie-summary {
    color:#999;
    font-size: .9rem;
    line-height: 1.3;
  }
}